.full-screen{
    width: 100%;
    height: 100vh;
    background: rgb(229,228,228);
    position: relative;
    z-index: 101;
}

.loading{
    width: 300px;
    height: 300px;
    z-index: 102;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}


.self-intro{
    width: 100%;
    background: rgb(192,192,192, .4);
    padding: 70px 30px;
}

.future-course{
    width: 100%;
    background: rgb(255,255,255);
    padding: 70px 30px;
}

.pet{
    width: 100%;
    background: rgb(192,192,192, .4);
    padding: 70px 30px;
}

.course{
    font-size: 15px;
    color: black;
}

.hero{
    width: 100%;
    height: 100vh;
    background: url(./../img/jp/image23.JPG) top center;
    background-size: cover;
    position: relative;
    margin-top: -20px;
    z-index: -1;
  }

.content{
    margin-left: auto;
    margin-right: auto;
    max-width: 700px
}

.black-20p{
    font-size: 20px;
    color:black;
    font-family: 'Lato', sans-serif;
}

.grey-36p{
    font-size: 36px;
    color: grey;
    font-family: 'Montserrat',sans-serif;
}

.grey-24p{
    font-size: 24px;
    color: grey;
    font-weight: bold;
    font-style: italic;
    font-family: 'Montserrat',sans-serif;
}

.hero-container{
    position: absolute;
    bottom: 0;
    top: 150px;
    right: 0;
    display: flex;
    flex-direction: column;
  }

  .hero-caption{
    position: absolute;
    bottom: 0;
    top: 240px;
    right: -320px;
    display: flex;
    flex-direction: column;
  }

  @media screen and (max-width: 880px){
    .hero-caption{
        position: absolute;
        bottom: 0;
        top: 330px;
        right: -120px;
        display: flex;
        flex-direction: column;
      }

      .grey-24p{
        display: none;
    }

